import { FC } from 'react';

// Types
import { TestMetadata } from '../types';

// Components
import Container from '../Container';
import Badge from '../Badge';
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';

// Types
export interface FormReadOnlyFieldListValueProps {
  values?: string[];
  testMetadata?: TestMetadata;
}

// Styles
import './FormReadOnlyFieldListValue.scss';

const FormReadOnlyFieldListValue: FC<FormReadOnlyFieldListValueProps> = ({
  values = [],
  testMetadata,
}) => {
  if (values?.length === 0) {
    return <FormReadOnlyFieldTextValue testMetadata={testMetadata} />;
  }

  return (
    <Container className="sb-form-readonly-field-list-value">
      {values?.map((value, index) => {
        // It starts appending an index when we have more than one element.
        const dataTestId = `${testMetadata?.['data-testid']}${
          index > 0 ? '_' + index : ''
        }`;

        return (
          <Badge
            testMetadata={{
              ...testMetadata,
              'data-testid': dataTestId,
            }}
            key={index}
            color="base"
            light
          >
            {value}
          </Badge>
        );
      })}
    </Container>
  );
};

export default FormReadOnlyFieldListValue;
